@import url(https://fonts.cdnfonts.com/css/athelas);
@import url(https://fonts.googleapis.com/css?family=Open+Sans|Open+Sans+Condensed:300&display=swap);
/*
Athelas bold used for headers in all caps 26pt
Open Sans Condensed used as main text in content and fine print
Open Sans Condensed Bold used in main text to highlight important words/sentences in content and headers and sub headers 22pt
Open Sans used for main text and showroom names 12pt
#E9BD1E colonial yellow
#FED41D bright yellow
#424242 charcoal grey*/

body {
	font-family: 'Open Sans Condensed', sans-serif;
}

#main-container {
	padding: 15px;
	background-color: darkgray;
	height: 95vh;
}

.container {
	margin-top: 1rem;
}

#weather-container{padding:5px;background-color:#d8d5d5;border-radius:5px;box-shadow:0px 0px 4px 2px rgba(0,0,0,0.5)}#weather-header{font-size:32px;padding:2px;display:flex;justify-content:center;text-align:center;font-weight:bold;background-color:#d8d5d5;color:#000;border-bottom:2px solid black}.row{margin:0;padding:0}.row-header{text-align:left}.col-8{padding-right:0}#header-row{padding:5px;margin:auto;text-align:center;border-bottom:2px solid black}#card-container{width:300px;min-height:400px;position:relative}#date-header{text-align:center;font-size:18px;font-weight:bold;padding:5px;border-bottom:2px solid black}#current-temp{margin:0;padding:5px;display:flex;align-items:center;justify-content:center;font-size:38px}#temp-icon-footer{text-align:center;padding:2px;display:flex;justify-content:center;align-items:center}#temp-icon{padding:2px;align-items:center;justify-content:center;display:flex}#weather-body{border-bottom:2px solid black}.forecast.col-12{padding:2px;list-style:none;display:inline-block;align-items:stretch}.col-4{display:flex;justify-content:center;align-items:center}

.card-deck{justify-content:space-between}.card{border-radius:0.5rem;box-shadow:0px 0px 4px 2px rgba(0,0,0,0.5)}.card-header{text-align:center;font-size:18px;background-color:black;color:goldenrod;padding:2px;font-weight:bold}.card-body{background-color:#c0c0c0;color:#000}.card-text{text-align:center;font-size:22px;font-weight:bold}#card-container{width:300px;min-height:400px;position:relative}

.table-responsive{box-shadow:0px 0px 2px 4px rgba(0,0,0,0.5);margin:5px}.table-header{background-color:black;color:goldenrod}.table-row{background-color:#d8d5d5;color:#000;font-weight:bold}#cutter,#saw{text-align:left;padding-left:10px;vertical-align:middle}#cut-row-header,#saw-row-header{text-align:left;padding-left:10px}#saw-table,#cutter-table{margin:0;font-weight:bold;text-align:center;vertical-align:middle}#loading{text-align:center;padding-top:50px;font-size:48px;font-weight:bolder}td{font-weight:bold;text-align:center;vertical-align:middle}

